.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.container {
  max-width: 600px;
  margin: 50px auto;
}

.grid {
  display: grid;
  grid-template-columns: 1fr;
}

.bg-div {
  background-image: url(https://picsum.photos/600/400);
  background-size: cover;
  box-shadow: 6px 4px 16px 0px rgba(0, 0, 0, 0.7);

  border-radius: 15px;
  overflow: hidden;
}
h1 {
  background-color: rgba(0, 0, 0, 0.363);
  color: white;
  padding: 18px;
  text-transform: uppercase;
}
h1 span {
  color: black;
  text-transform: none;
}
img {
  width: 100%;
}

button {
  font-size: 18px;
  background-color: black;
  color: white;
  cursor: pointer;
  border-radius: 5px;
  padding: 5px 15px;

  transition: all 0.2s;
}
button:hover,
button:active {
  transform: scale(1.1);
  box-shadow: 0px 0px 10px 1px rgba(0, 0, 0, 0.8);
}

@media only screen and (max-width: 770px) {
  .container {
    padding: 0px 15px;
  }

  h1 {
    font-size: 16px;
  }
}
